import React, { useState } from "react";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import { closeSection } from "./../helper/helpers";
import  AccordionItem  from "./inc/AccodianItem";

const Faq = ({ data }) => {



  return (
    <OverlayScrollbarsComponent
      options={{ scrollbars: { autoHide: "scroll" } }}
      className="faq section-panel"
      id="faq"
    >
      <button className="panel-close-btn" onClick={() => closeSection("faq")}>
        <i className="fa-light fa-xmark"></i>
      </button>
      <div className="faq-content">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-12">
              <h2 className="section-title title-center">
                Frequently asked <span>question</span>
              </h2>
            </div>
          </div>
          <div className="row justify-content-center">
            <div className="col-xl-8 col-lg-10">
              <div className="faq-box">
                {
                  
                 Object.values(data).map((faq, i) => <AccordionItem key={i} question={faq.question} answer={faq.answer} />)
                }
              
              </div>
            </div>
          </div>
        </div>
      </div>
    </OverlayScrollbarsComponent>
  );
};

export default Faq;
