import React, { useState, useEffect } from 'react';

const Loader = ({isLoading}) => {
  


 

        return (
            <div>
                { !isLoading ? <div className="preloader"> <img src='assets/images/loader.gif' alt="loading" />  </div> : null }
            </div>
            
         
          
        )


}
    

export default Loader;