import React  from "react";
import Slider from "react-slick";

const ClientSlider = ( { clients, api }) => {

  const getClientData = (client) => {
    if (client) {
      
      let bb = client.company_logo;
      if (bb) {
        let cv_obj = JSON.parse(bb);
        if (cv_obj) {
          return <img src={api + "/" + cv_obj[0].download_link} alt={cv_obj[0].original_name} />     
        }
      }
    }
  };


    var settings = {
        dots: false,
        arrows: false,
        infinite: true,
        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 1,
        initialSlide: 1,       
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      };
      return (

      


        <Slider {...settings} className="client-slider">
        { (clients) ? Object.values(clients).map( (client, i ) => (
          <div className="single-client" key={i}>
            { getClientData(client) }
        </div>
        ) ) : '' }
        
 </Slider>
      );
}

export default ClientSlider