import React from "react";
import {  showModal, getDate } from "./../../helper/helpers";

const BlogGridItem = ({ post, thumb, post_title, post_date, post_author }) => {

  return (
    <div className="col-lg-4 col-md-6 col-sm-8">
      <div className="single-blog">
        <div className="part-img">
          <img src={thumb} alt="Image" />
        </div>
        <div className="part-txt">
          <h3>
           
            <a href="#" onClick={() => showModal({post}, thumb, post_author) }>
             {post_title}
            </a>
          </h3>
          <ul>
            <li>
              <span>
                <i className="fa-duotone fa-user-pen"></i>
              </span>
              {post_author}
            </li>
            <li>
              <span>
                <i className="fa-duotone fa-calendar"></i>
              </span>
              {getDate(post_date)}
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default BlogGridItem;
