import { render } from "@testing-library/react";
import React, { useEffect, useState } from "react";

const PortfolioGrid = ({
  cat_class,
  cat_name,
  photo,
  title,
  start_date,
  end_date,
  rating,
  url,
}) => {
  const Star = ({ rating }) => {
    return (
      <div className="star">
        <i
          className={
            rating > 0
              ? "fa-solid fa-star-sharp rated"
              : "fa-solid fa-star-sharp"
          }
        ></i>
        <i
          className={
            rating > 1
              ? "fa-solid fa-star-sharp rated"
              : "fa-solid fa-star-sharp"
          }
        ></i>
        <i
          className={
            rating > 2
              ? "fa-solid fa-star-sharp rated"
              : "fa-solid fa-star-sharp"
          }
        ></i>
        <i
          className={
            rating > 3
              ? "fa-solid fa-star-sharp rated"
              : "fa-solid fa-star-sharp"
          }
        ></i>
        <i
          className={
            rating > 4
              ? "fa-solid fa-star-sharp rated"
              : "fa-solid fa-star-sharp"
          }
        ></i>
      </div>
    );
  };

  return (
    <div className={"custom-col " + cat_class}>
      <div className="single-portfolio-box">
        <div className="part-img">
          <img src={photo} alt="Image" />
        </div>
        <div className="part-txt">
          <span className="category">{cat_name}</span>
          <h3>{title}</h3>
          <ul className="info">
            <li>
              <span>Start:</span> {start_date}
            </li>
            <li>
              <span>End:</span> {end_date}
            </li>
          </ul>
          <div className="bottom">
            <Star rating={rating} />

            <a target="_blank" href={url}>
              Visit Site{" "}
              <span>
                <i className="fa-regular fa-arrow-right"></i>
              </span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PortfolioGrid;
