import React, { useState } from "react";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import { closeSection } from "./../helper/helpers";


import { useForm } from 'react-hook-form';

const Contact = ({ data, api }) => {
  const [resMessage, setResMessage] = useState("");

  const { register, handleSubmit, formState: { errors } } = useForm();
  const onSubmit = formData => {
    
    if(formData){
      fetch(process.env.REACT_APP_API + '/contact-with-me',{
      method: 'POST',
      headers: {
        "Content-Type" : "application/json",
        "accept" : "application/json"},
         body: JSON.stringify(formData)
       }).then(async response => {
         if (!response.ok) {
         const validation = await response.json();
         setResMessage("Request sent successfully");
         console.log(validation.errors);
       }else{
        
       }
    });

    }
   
    
  } 
  //

  return (
    <OverlayScrollbarsComponent
      options={{ scrollbars: { autoHide: "scroll" } }}
      className="contact section-panel"
      id="contact"
    >
      <button
        className="panel-close-btn"
        onClick={() => closeSection("contact")}
      >
        <i className="fa-light fa-xmark"></i>
      </button>
      <div className="contact-content">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-xl-6 col-lg-5">
              <h2 className="section-title title-center mb-3">
                get in touch with <span>me</span>
              </h2>
              <div className="about">
              <div className="about-content pt-2 pb-4">
                <div className="part-txt text-center"><p>{ (data) ? data.contact_speech : '' }</p></div>
              </div>
              </div>
            </div>
          </div>
          <div className="row g-0 justify-content-center">
            <div className="col-lg-6 col-md-7 col-sm-6 order-e">
            

              <form className="contact-form" onSubmit={handleSubmit(onSubmit)}>
                <div className="form-group ">
                  <label>Your full name</label>
                  <input type="text" name="name" {...register("name", {required: 'Name is required', maxLength: 80 })} />
                  {errors.name && <div className="form-text text-danger">{errors.name.message}</div>}
                </div>
                <div className="form-group ">
                  <label>Your email address</label>
                  <input type="email" name="email" {...register("email", {required: 'Email is required', pattern: /^\S+@\S+$/i})} />
                  {errors.email && <div className="form-text text-danger">{errors.email.message}</div>}
                </div>
                <div className="form-group ">
                  <label>Subject</label>
                  <input type="text" name="subject" {...register("subject", {required: 'Subject is required', maxLength: 100})} />
                  {errors.subject && <div className="form-text text-danger">{errors.subject.message}</div>}
                </div>
                <div className="form-group ">
                  <label>Write your message</label>
                  <textarea name="message" {...register("message", {required: 'Message is required', maxLength: 1000})}></textarea>
                  {errors.message && <div className="form-text text-danger">{errors.message.message}</div>}
                </div>
                <button type="submit" className="def-btn">Send Message</button>

				{ (resMessage) ? <div className="alert alert-success mt-4" role="alert">{resMessage}</div> : '' } 
               

              </form>
            
            </div>
            <div className="col-lg-4 col-md-5 col-sm-6">
              <div className="contact-info">
                <div className="single-box">
                  <div className="part-icon">
                    <i className="fa-duotone fa-phone"></i>
                  </div>
            
                    { (data && data.phone) ? <div className="part-txt"  dangerouslySetInnerHTML={{ __html: data.phone }} /> : '' }
                    
                  
                </div>
                <div className="single-box">
                  <div className="part-icon">
                    <i className="fa-duotone fa-envelope"></i>
                  </div>
              
                  { (data && data.email) ? <div className="part-txt" dangerouslySetInnerHTML={{ __html: data.email }} /> : '' }
                 
                </div>
                <div className="single-box">
                  <div className="part-icon">
                    <i className="fa-duotone fa-map-location-dot"></i>
                  </div>
                  <div className="part-txt">
                    <p>{ (data && data.address) }</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row g-0 justify-content-center pt-4" >
            <div className="col-12 pt-4  pb-4">
            { (data && data.map ) ? <div style={{ borderRadius: '10px', overflow: 'hidden' }} dangerouslySetInnerHTML={{ __html: data.map }} /> : '' }
            </div>
          
          </div>
        </div>
        <div className="">
        
        </div>
      </div>

    

    </OverlayScrollbarsComponent>
  );
};

export default Contact;
