const CircleBar = (props) => {
  return (
    <div className="progress-box">
      <div className="circle-progressbar">
      
        <svg viewBox="0 0 36 36" className="circular-chart">
          <defs>
            <linearGradient id="gradient">
              <stop offset="0%" stopColor="#CB26B6" />
              <stop offset="60%" stopColor="#F8B127" stopOpacity="1" />
            </linearGradient>
          </defs>
          <path
            stroke="url(#gradient)"
            className="circle-bg"
            d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
          />
          <path
            stroke="url(#gradient)"
            className="circle"
            strokeDasharray={props.value + ", 100"}
            d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
          />
          
        </svg>
          <div className="txt">
            <span className="percent">{props.value}%</span>
            <p>{props.text}</p>
          </div>
   

        
      </div>
    </div>
  );
};

export default CircleBar;
