import React, { useEffect, useState } from "react";


import { OverlayScrollbarsComponent } from "overlayscrollbars-react";

import CircleBar from "./inc/CircleBar";
import ExperienceGrid from "./inc/ExperienceGrid";
import OdometerItem from "./inc/OdometerItem";
import Expert from "./inc/Expert";

import { closeSection } from "./../helper/helpers";

const About = ({ data, api }) => {
  return (
    <div>
      <OverlayScrollbarsComponent
        className="about section-panel"
        id="about"
        options={{ scrollbars: { autoHide: "scroll" } }}
      >
        <button
          className="panel-close-btn"
          onClick={() => closeSection("about")}
        >
          <i className="fa-light fa-xmark"></i>
        </button>

        <div className="about-content">
          <div className="container">
            <div className="row align-items-center justify-content-between">
              <div className="col-lg-5 col-md-6">
                <div className="part-img">
                  { (data && data.photo) ?  <img src={ api + '/' + data.photo } alt="image" /> : ''  }
                </div>
              </div>
              <div className="col-lg-6 col-md-6">
                <div className="part-txt">
                  <h2 className="section-title">
                    About <span>Me</span>
                  </h2>
                  <p>I'm a professional {data ? data.work_on : ""}.</p>
                  <p>{data ? data.description : ""}</p>
                  <div className="progress-wrap">
                    {data && data.skills
                      ? data.skills.map((skill, skill_index) => (
                          <CircleBar
                            key={skill_index}
                            value={skill.skill_point}
                            text={skill.language_name}
                          />
                        ))
                      : ""}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="service">
        <div className="service-content">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-xl-6 col-lg-5">
                <h2 className="section-title title-center">
                  <span>Why</span> choose me
                </h2>
              </div>
            </div>
            <div className="row g-md-4 g-sm-3 g-4 justify-content-between">
              {
                (data && data.choose) ? Object.values(data.choose).map((chos, ci) => (
                  <Expert
                key={ci}
                icon={chos.icon}
                title={chos.title}
                description={chos.description}
              /> 
                ) ) : ''
              }
              
              
            </div>
          </div>
        </div>
        </div>

        <div className="fun-fact">
          <div className="container">
            <div className="row g-0 gy-4">
              {data && data.counters
                ? Object.values(data.counters).map((single, count) => (
                    <OdometerItem
                      key={count}
                      text={single.title}
                      icon={single.icon}
                      value={single.value}
                    />
                  ))
                : ""}
            </div>
          </div>
        </div>
        <div className="education">
          <div className="container">
            <div className="row rg-60">
              <div className="col-md-6">
                <h2 className="section-title">Experience</h2>
                <ul>
                  {data &&
                    data.experiences &&
                    data.experiences.map((experience, index_e) => (
                      <ExperienceGrid
                        key={index_e}
                        title={experience.company_name}
                        duration={experience.duration}
                        company={experience.location}
                      />
                    ))}
                </ul>
              </div>
              <div className="col-md-6">
                <h2 className="section-title">Education</h2>
                <ul>
                  {data &&
                    data.educations &&
                    data.educations.map((education, index_e) => (
                      <ExperienceGrid
                        key={index_e}
                        title={education.institute_name}
                        duration={education.duration}
                        company={education.location}
                      />
                    ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </OverlayScrollbarsComponent>
    </div>
  );
};

export default About;
