import React, { useEffect } from "react";
import Slider from "react-slick";
import TestimonialItem from  './TestimonialItem'

const SimpleSlider = ({ testimonials, api }) => {


  useEffect(() => {
    const sample = document.querySelectorAll(".slick-slide");

    for (let i = 0; i < sample.length; i++) {
      sample[i].classList.add("single-feedback");
    }
  });

  const SamplePrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <button onClick={onClick} className="slick-arrow slick-prev">
        <i className="fa-regular fa-left-long"></i>
      </button>
    );
  };

  const SampleNextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <button onClick={onClick} className="slick-arrow slick-next">
        <i className="fa-regular fa-right-long"></i>
      </button>
    );
  };

  const CustomSlide = ({ photo, name, designation, description, api }) => {
    return (
      <TestimonialItem photo={photo} name={name} designation={designation} description={description} api={api} />
    );
  };

  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    className: "subhanallah",
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,

    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 1,
    beforeChange: (oldIndex, newIndex) => {
      setTimeout(() => {
        const sample = document.querySelectorAll(".slick-slide");

        for (let i = 0; i < sample.length; i++) {
          sample[i].classList.add("single-feedback");
          // console.log("beforeChange", oldIndex, newIndex);
        }
      });
    },
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <Slider {...settings} className="testimonial-slider">
      {
        (testimonials) ? Object.values(testimonials).map( (slide, i) => (
          <CustomSlide api={api} photo={slide.photo} name={slide.name} designation={slide.position} key={i} index={slide.id} description={slide.message} />
        )) : '' 
      }
    </Slider>
  );
};

export default SimpleSlider;
