import React, { useEffect } from "react";
import { closeModal } from "./../../helper/helpers";

export default function BlogDetailModal() {
  return (
    <div
      className="modal fade"
      id="blogModal"
      tabIndex="-1"
      aria-labelledby="blogModalLabel"
      aria-modal="true"
      role="dialog"
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="blogModalLabel">
              My Article Collection
            </h5>
            <button type="button" onClick={() => closeModal()}>
              <i className="fa-light fa-xmark"></i>
            </button>
          </div>
          <div className="modal-body">
            <div className="blog-details">
              <div className="part-img">
                <img src="assets/images/blog-details.jpg" id="modelPostImg" alt="Image" />
              </div>
              <div className="part-txt">
                <h2 className="blog-title" id="modelPostTitle"></h2>
                <ul>
                  <li>
                    <span>
                      <i className="fa-duotone fa-user-pen"></i>
                    </span>
                    <label id="modelPostAuthor"></label>
                  </li>
                  <li>
                    <span>
                      <i className="fa-duotone fa-calendar"></i>
                    </span>
                    <label id="modelPostDate"></label>
                  </li>
                </ul>
                <div className="blog-description" id="modelPostDescription"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
