import React, { useState, useRef, useEffect } from "react";

export function closeSection(sectionName) {
  document.getElementById(sectionName).classList.remove("active");
  document.querySelector(".header").style.display = "block";
}

export function openSection(sectionName) {
  document.getElementById(sectionName).classList.add("active");
  document.querySelector(".header").style.display = "none";
}

export function viewMode() {
  var mode = document.getElementById("darkMode");
  if (mode) {
    document.getElementById("darkMode").remove();
    document.getElementById("mode-change").innerHTML =
      '<i class="fa-solid fa-moon"></i>';
    window.localStorage.setItem("view_mode", "");
  } else {
    var css = document.createElement("link");
    css.setAttribute("rel", "stylesheet");
    css.setAttribute("id", "darkMode");
    css.setAttribute("type", "text/css");
    css.setAttribute("href", "assets/css/style-dark.css");
    document.head.appendChild(css);
    document.getElementById("mode-change").innerHTML =
      '<i class="fa-regular fa-moon"></i>';
    window.localStorage.setItem("view_mode", "dark");
  }
}

export function showModal( post, thumb, post_author ) {

  let post_details = post.post;
  let element = document.getElementById("blogModal");
  
 
  element.classList.add("show", "d-block");

  let modal_drop = document.createElement("div");
  modal_drop.classList.add("modal-backdrop", "fade", "show");
  document.querySelector(".App2").appendChild(modal_drop);
  
  document.getElementById('modelPostTitle').innerHTML = post_details.title;
  document.getElementById('modelPostDescription').innerHTML = post_details.description;
  document.getElementById('modelPostImg').setAttribute('src', thumb );
  document.getElementById('modelPostDate').innerHTML = getDate(post_details.date);
  document.getElementById('modelPostAuthor').innerHTML =post_author;
   
  

}

export function closeModal() {
  let element = document.getElementById("blogModal");
  element.classList.remove("d-block", "show");
  element.removeAttribute('post-id');
  let modal_drop = document.querySelector(".modal-backdrop");
  modal_drop.remove();
}

export function getDateFormat(get_date) {
  const new_date = new Date(get_date).toLocaleDateString();
  return new_date;
}

export function getDate(get_date) {
  const event = new Date(get_date);
const options = {  year: 'numeric', month: 'short', day: 'numeric' };
return event.toLocaleDateString('en-GB', options);
  
}
