import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

import { openSection } from "./../helper/helpers";

const Banner = ({ data, api }) => {

  const cvFile = () => {
    if (data) {
      let bb = data.cv_download;
      if (bb) {
        let cv_obj = JSON.parse(bb);
        if (cv_obj) {
          // let a = document.createElement('a');
          //a.href =  api + '/' + cv_obj[0].download_link;
          download_file(api + "/" + cv_obj[0].download_link, "sazzad");
          //a.click();
        }
      }
    }
  };

  const download_file = (fileURL, fileName) => {
    // for non-IE
    if (!window.ActiveXObject) {
      var save = document.createElement("a");
      save.href = fileURL;
      save.target = "_blank";
      var filename = fileURL.substring(fileURL.lastIndexOf("/") + 1);
      save.download = fileName || filename;
      if (
        navigator.userAgent.toLowerCase().match(/(ipad|iphone|safari)/) &&
        navigator.userAgent.search("Chrome") < 0
      ) {
        document.location = save.href;
        // window event not working here
      } else {
        var evt = new MouseEvent("click", {
          view: window,
          bubbles: true,
          cancelable: false,
        });
        save.dispatchEvent(evt);
        (window.URL || window.webkitURL).revokeObjectURL(save.href);
      }
    }

    // for IE < 11
    else if (!!window.ActiveXObject && document.execCommand) {
      var _window = window.open(fileURL, "_blank");
      _window.document.close();
      _window.document.execCommand("SaveAs", true, fileName || fileURL);
      _window.close();
    }
  };

  return (
    <div className="banner">
      <div className="main-social">
      
        <a
          href={(data && data.socials ) ? data.socials.facebook : '' }
          target="_blank"
          title="Facebook"
        >
          <i className="fa-brands fa-facebook-f"></i>
        </a>
        <a href={(data && data.socials ) ? data.socials.github : '' } target="_blank" title="Twitter">
        <i className="fa-brands fa-github"></i>
        </a>
        <a href={(data && data.socials ) ? data.socials.linkedin : '' } target="_blank" title="Linkedin">
          <i className="fa-brands fa-linkedin-in"></i>
        </a>
        <a href={(data && data.socials ) ? data.socials.skype : '' } target="_blank" title="Skype">
          <i className="fa-brands fa-skype"></i>
        </a>
      </div>
      <div className="container">
        <div className="row align-items-center">
          <div className="col-md-6 nav-col">
            <div className="nav-wrap">
              <div className="nav-grid">
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("about")}
                    className="section-btn"
                    data-section="about"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-circle-user"></i>
                    </span>
                    <span className="txt">ABOUT</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("service")}
                    className="section-btn"
                    data-section="service"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-gear"></i>
                    </span>
                    <span className="txt">SERVICE</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("portfolio")}
                    className="section-btn"
                    data-section="portfolio"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-folder-open"></i>
                    </span>
                    <span className="txt">PORTFOLIO</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("faq")}
                    className="section-btn"
                    data-section="faq"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-circle-question"></i>
                    </span>
                    <span className="txt">FAQ</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("gallery")}
                    className="section-btn"
                    data-section="gallery"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-image"></i>
                    </span>
                    <span className="txt">GALLERY</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("blog")}
                    className="section-btn"
                    data-section="blog"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-file-pen"></i>
                    </span>
                    <span className="txt">BLOG</span>
                  </a>
                </div>
                <div className="single-item">
                  <a
                    role="button"
                    onClick={() => openSection("contact")}
                    className="section-btn"
                    data-section="contact"
                  >
                    <span className="icon">
                      <i className="fa-thin fa-user-plus"></i>
                    </span>
                    <span className="txt">CONTACT</span>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-6">
            <div className="banner-txt">
              <h3>Hello!</h3>
              <h1>
                <span>I'm</span> {data ? data.site_name : ""}.
              </h1>
              <h2>{data ? data.work_on : ""}</h2>
              <p>{data ? data.description : ""}</p>
              <div className="btn-box">
                <a
                  href="#"
                  onClick={() => openSection("about")}
                  className="section-btn def-btn"
                >
                  <i className="fa-light fa-user"></i> About Me
                </a>

                <a onClick={cvFile} href="#" className="def-btn">
                  <i className="fa-light fa-file-lines"></i> Download CV
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="copyright">
        <p dangerouslySetInnerHTML={{ __html: data ? data.copyright : "" }}></p>
      </div>
    </div>
  );
};

export default Banner;
