import  React,  { useEffect, useState } from 'react';
import logo from './../assets/images/logo.png'
import {viewMode} from './../helper/helpers'

const Header = ({ data, api }) => {


  useEffect(() => {    
    let hasMode = window.localStorage.getItem('view_mode');
    if( hasMode && (hasMode == 'dark')){
        var mode = document.getElementById('darkMode');
            if(!mode){
                var css = document.createElement("link");
                css.setAttribute("rel", "stylesheet");
                css.setAttribute("id", "darkMode");
                css.setAttribute("type", "text/css");
                css.setAttribute("href", "assets/css/style-dark.css");
                document.head.appendChild(css);
                document.getElementById('mode-change').innerHTML = '<i class="fa-regular fa-moon"></i>'
            
            }
            }
  }, [])
  
    

    return (
        <div className="header">
        <div className="container">
            <div className="row justify-content-between align-items-center">
                <div className="col-6">
                    <div className="logo">
                        <a to="/">
                            <img id="logo" src={`${api}/${data.logo}`} alt="LOGO" />
                        </a>
                    </div>
                </div>
                <div className="col-6">
                    <div className="nav-btn">                            
                        <a role="button" id="mode-change" onClick={ () => viewMode() }><i className="fa-solid fa-moon"></i></a>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default Header;