import React, { useState } from "react";


const AccordionItem = ( {question, answer }) => {
    const [isActive , setActive ] = useState(false)

    

  return (
    <div className="single-card">
      <div className="faq-header" onClick={() => setActive(!isActive)}>
        <h3>{question}</h3>
        <div className="icon">
          <i className={(isActive) ? 'fa-light fa-minus' : 'fa-light fa-plus' }></i>
        </div>
      </div>
      <div className={ (isActive) ? 'faq-body d-block' : 'faq-body d-none' }>
        <div dangerouslySetInnerHTML={{__html: answer }} />
      </div>
    </div>
  );
};

export default AccordionItem;
